import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './plugins/element.js'
// 加载全局css样式
import './assets/css/global.css'
// 导入
import axios from 'axios'
// 导入通用js
import common from './CommonFunction/common.js'
import global from './CommonFunction/global.js'
import clipboardJS from 'clipboard'
import { version, importManager } from 'less'

// 让ajax可以携带cookie
axios.defaults.withCredentials = true
// axios.defaults.baseURL = 'http://hmj.mbg17.cn:8888/autoTest/platform'
// axios.defaults.baseURL = 'http://192.168.80.40:8989/quanwai/test/'
// axios.defaults.baseURL = 'http://localhost:8989/'
axios.defaults.baseURL = 'http://www.haomingjian.top:8989/'
axios.interceptors.request.use(request => {
  /*判断token存在   登录拦截*/
  if(window.localStorage.getItem("token")){
    /*设置统一的header*/
    request.headers.Authorization  = window.localStorage.getItem("token");
    // request.headers.cookie = "_iqt=9urefamxyv6c5rpj";
  }
  return request;
});
// 响应拦截
axios.interceptors.response.use((response) =>{
  if(response.status !== 200 ){
    vm.$message.error("后台错误，请查看后台日志");
    return response;
  }
  if(response.data.code !== 10000){
    if(response.data.code === 40009){
      // 跳转到登录页面
      vm.$router.push('/login');
    }else{
      vm.$message.error(response.data.msg);
    }
  }
  return response;
})
Vue.prototype.$clipboard = clipboardJS
Vue.prototype.$http = axios
Vue.prototype.$common = common
Vue.prototype.$global = global
Vue.config.productionTip = false

const vm = new Vue({
  router,
  render: h => h(App)
}).$mount('#app')

export default vm
