<script>
// import componentVue from "../caseConfig/component.vue";
import qs from 'qs'
export default {
  data() {
    return {
      dialog: false,
      riseId: "",
      loading: false,
      studyProgressDialog: false,
      userLearningRecord: {},
      allLessonPackage: [],
      packageCode: "",
      lessonPackageAcquireList: [],
      acquireLessonPackageCodes:[],
      queryParams:{

      }
    };
  },
  methods: {
    async getStudyProgress(lessonCode, termScheduleCode) {
      this.studyProgressDialog = true;
      const response = await this.$common.get(
        "/classMemberRegistration/getStudyProgress?lessonCode=" +
          lessonCode +
          "&termScheduleCode=" +
          termScheduleCode +
          "&riseId=" +
          this.riseId
      );
      this.userLearningRecord = response.data;
    },
    studyProgressDialogClose() {
      this.studyProgressDialog = false;
    },

    async getAllProLessonPackage() {
      const response = await this.$common.get(
        "/lessonPackage/getProALLLessonPackage"
      );
      if (response.code == 10000) {
        this.allLessonPackage = response.data;
      }
    },
    async queryAcquire() {
      this.loading = true;
      const response = await this.$common.get(
        "/lessonPackage/getPackageAcquireRules?lessonPackageCode=" +
          this.queryParams.packageCode + "&acquireLessonPackageCodes=" + this.queryParams.acquireLessonPackageCodes
      );
      this.lessonPackageAcquireList = response.data;
      this.loading = false;
    }
  },

  created() {
    this.getAllProLessonPackage();
  }
};
</script>
