      </el-card>

      <el-table
      v-loading="loading"
      element-loading-text="拼命加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
    :data="userData"
    border
    style="width: 100%">


    <el-table-column type="expand">
      <template slot-scope="props">
        <el-table :data="props.row.lessonContents" height="500" border style="width: 100%">
              <el-table-column prop="lessonTitle" label="课程名称" ></el-table-column>
              <el-table-column prop="lessonTypeName" label="课程类型" ></el-table-column>
              <el-table-column prop="lessonCode" label="lessonCode"></el-table-column>
              <el-table-column prop="finishRule" label="完课规则"></el-table-column>
              <el-table-column prop="unlockRule" label="解锁规则"></el-table-column>
              <el-table-column prop="lessonOpeningTime" label="开课时间"></el-table-column>
              <el-table-column prop="lessonEndTime" label="结课时间"></el-table-column>
              <el-table-column label="更多操作">
                <template slot-scope="scope">
                  <!-- <el-button type="primary" size="mini" plain @click="getStudyProgress(scope.row.lessonCode)">查看详情</el-button> -->
