    <el-dialog
  title="学习进度"
  :visible.sync="studyProgressDialog"
  width="30%"
  :before-close="studyProgressDialogClose">
  <span v-if="studyProgressDialog"> 课程状态：{{(userLearningRecord.userLessonCompletion && userLearningRecord.userLessonCompletion.status==0)? '已完成':'未完成'}}，课程学习进度： {{userLearningRecord.userLessonLearningRecord.sectionProgress}} / {{userLearningRecord.userLessonLearningRecord.sectionTotal}} </span>
  <span slot="footer" class="dialog-footer">
    <el-button type="primary" @click="studyProgressDialog = false">朕知道了</el-button>
  </span>
</el-dialog>
   
  </div>
</template>
<script>
import componentVue from "../caseConfig/component.vue";
export default {
  data() {
    return {
      dialog:false,
      riseId:'',
      userData:[],
      loading:false,
      studyProgressDialog:false,
      userLearningRecord:{},
      userInfo:{}
    }
  },
  methods: {
     async getUserClassMemberRegistration(){
       this.loading = true;
       const response = await this.$common.get("/classMemberRegistration/getOnlineUserInfo?riseId=" + this.riseId);
       this.userData = response.data;
       this.userInfo = response.extend;
       this.loading = false;
       
     },
     async getStudyProgress(lessonCode,termScheduleCode){
       const response = await this.$common.get("/classMemberRegistration/getStudyProgress?lessonCode=" + lessonCode + "&termScheduleCode=" + termScheduleCode + "&riseId=" + this.riseId);
       this.userLearningRecord = response.data;
       this.studyProgressDialog = true;
     },
     studyProgressDialogClose(){
       this.studyProgressDialog = false;
     },
     copyPassportId(){
      // console.log(this.userInfo.passportId);
      let clipboard = new this.$clipboard('#copyPassportId');
        clipboard.on('success', e => {
            this.$message({ type: 'success', message: '复制成功' });
            // 释放内存
            clipboard.destroy();
        });
        clipboard.on('error', e => {
            // 不支持复制
            this.$message({ type: 'warning', message: '该浏览器不支持自动复制' });
                // 释放内存
            clipboard.destroy();
        });
     }
  },

  created() {

  },
};
</script>
